<template>
    <div class="title">
        <i class="el-icon-s-order"></i>
        <span>{{name}}</span>
        <span
            class="time"
            style="position: absolute; top: 0px; right: 20px;"
        >数据上传时间:{{times}}</span>
    </div>
</template>

<script>
export default {
    data() {
        return {};
    },
    props: {
        name: {
            type: String,
            required: false
        },
         times: {
            type: String,
            required: false
        }
    },
    components: {},
    created() {},
    mounted() {},
    methods: {}
};
</script>

<style scoped lang='scss'>
.title{
    position: relative;
    padding: 0 20px;
    height: 40px;
    line-height: 40px;
    font-size: 16px;
    font-weight: bolder;
    background: #DEDEDE;
    .time{
        font-size: 14px;
    }
}
</style>



